<template>
  <div class="wrapper">
    <Card>
      <h2>Login</h2>
      <BaseForm :form-data="formData">
      </BaseForm>
      <a class="action" href="./#/dashboard">Login</a>
    </Card>
  </div>
</template>

<script>
import Card from './Card'
import BaseForm from './BaseForm'

export default {
  components: { Card, BaseForm },
  data () {
    return {
      formData: [
        {
          inputType: 'text',
          label: 'Username',
          placeholder: 'Username'
        },
        {
          inputType: 'password',
          label: 'Password',
          placeholder: 'Password'
        }
      ]
    }
  }
}
</script>

<style scoped>
  .wrapper {
    width: 450px;
    margin: auto;
    transform: translate(0, 50%);
  }
</style>



