<template>
  <div class="sidebar">
    <div class="items-top">
      <SidebarItem  
        v-for="(item, index) in sidebarTopItems" :key="index" 
        :item-data="item" class="sidebar-item">
      </SidebarItem>
    </div>
    <div class="items-bototm">
      <SidebarItem  
        v-for="(item, index) in sidebarBottomItems" :key="index" 
        :item-data="item" class="sidebar-item">
      </SidebarItem>
    </div>
  </div>
</template>

<script>
import SidebarItem from './SidebarItem'

export default {
  components: { SidebarItem },
  data () {
    return {
      sidebarTopItems: [
        {
          name: 'home',
          title: 'Dashboard'
        },
        {
          name: 'plus',
          title: 'Add DVR/IP Cam'
        },
      ],
      sidebarBottomItems: [
        {
          name: 'settings',
          title: 'Settings'
        }
      ]
    }
  }
}
</script>

<style scoped>
.sidebar {
  white-space: nowrap;
  overflow-x: hidden;
  position: fixed;
  z-index: 100;
  left: 15px;
  width: 60px;
  height: 84%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #42b883;
  border-radius: 5px;
  transition-duration: 0.3s;
}
.sidebar:hover {
  width: 200px;
}
@media screen and (max-width: 750px) {
  .sidebar {
    display: none;
  }
}
</style>



