<template>
  <div class="form">
    <div class="input-field" v-for="(label, index) in labels" :key="index">
      <span class="input-label">
        {{ label }}
      </span>
      <span class="input-box">
        <input :type="inputTypes[index].toLowerCase()"
               :class="classes[index]"
               :placeholder="placeholders[index]"
               :style="getStyle(index)"
               :disabled="checkDisabled(index)"
               @click="toggleUsername(index, $event)">
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['formData'],
  data () {
    return {
      disableUsername: false
    }
  },
  methods: {
    getStyle (index) {
      let styles = {}
      let inputType = this.inputTypes[index].toLowerCase()
      if (inputType === 'text' || inputType === 'password') {
        styles.width = '100%'
      }
      if (this.names[index] === "username" || this.names[index] === "password") {
        if (this.disableUsername) {
          styles.backgroundColor = '#32344e'
        }
      }
      return styles
    },
    toggleUsername (index, event) {
      if (this.formData[index].inputType.toLowerCase() === 'checkbox') {
        if (event.target.checked) {
          this.disableUsername = true
        } else {
          this.disableUsername = false
        }
      }
    },
    checkDisabled (index) {
      if (this.names[index] === "username" || this.names[index] === "password") {
        if (this.disableUsername) {
          return true
        }
      }
      return false
    }
  },
  computed: {
    labels () {
      let l = []
      for (let data of this.formData) {
        if (data.label) {
          l.push(data.label)
        } else {
          l.push('');
        }
      }
      return l
    },
    placeholders () {
      let l = []
      for (let data of this.formData) {
        if (data.placeholder) {
          l.push(data.placeholder)
        } else {
          l.push('');
        }
      }
      return l
    },
    inputTypes () {
      let l = []
      for (let data of this.formData) {
        if (data.inputType) {
          l.push(data.inputType)
        } else {
          l.push('');
        }
      }
      return l
    },
    classes () {
      let l = []
      for (let data of this.formData) {
        if (data.class) {
          l.push(data.class)
        } else {
          l.push('');
        }
      }
      return l
    },
    names () {
      let l = []
      for (let data of this.formData) {
        if (data.name) {
          l.push(data.name)
        } else {
          l.push('');
        }
      }
      return l
    }
  }
}
</script>

<style scoped>
  .form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .input-field {
    width: 50%;
    padding: 5px 5px 5px 5px;
  }
  .input-label {
    font-size: 14px;
    display: inline-block;
    width: 30%;
    /* text-align: right; */
    padding-right: 10px;
  }
  .input-box {
    display: inline-block;
    width: 65%;
  }
</style>



