<template>
  <div class="sidebar-item">
    <!-- <img v-if="itemData.name=='home'" 
         src="../assets/home.svg" alt="img">
    <img v-if="itemData.name=='plus'" 
         src="../assets/plus.svg" alt="img">
    <img v-if="itemData.name=='settings'" 
         src="../assets/settings.svg" alt="img"> -->
    <img :src="img" alt="img">
    <span>{{ itemData.title }}</span>
  </div>
</template>

<script>
export default {
  props: ['itemData'],
  computed: {
    img () {
      return require('../assets/'+this.itemData.name+'.svg')
    }
  }
}
</script>

<style scoped>
.sidebar-item {
  display: flex;
  flex-direction: row;
  align-content: center;
  overflow: hidden;
  transition-duration: 0.2s;
}
.sidebar-item:hover {
  background-color: #399c70;
}

.sidebar-item span {
  font-weight: 300;
  font-size: 16px;
  height: 55px;
  width: 130px;
  line-height: 60px;
  padding-left: 60px;
  /* text-align: center; */
  /* background-color: #00f; */
}

.sidebar-item img {
  position: fixed;
  height: 60px;
  width: 60px;
  padding: 15px;
  /* background-color: #f00; */
}
</style>



