import Home from '../components/Home'
import Login from '../components/Login'
import Dashboard from '../components/Dashboard'
import AddCam from '../components/AddCam'
import AddDVR from '../components/AddDVR'


const routes = [
  {
    path: '/', 
    component: Home
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/dashboard',
    component: Dashboard
  },
  {
    path: '/add-dvr',
    component: AddDVR
  },
  {
    path: '/add-cam',
    component: AddCam
  },
  {
    path: '*',
    redirect: '/'
  }
]

export default routes


