<template>
<div class="wrapper">
  <div class="arrow" :style="leftOffset"></div>
  <div class="dropdown-wrapper" :style="show">
    <div class="dropdown dropdown-bubble">
      <ul class="dropdown-menu">
        <li><p>1</p></li>
        <li><p>2</p></li>
        <li><p>3</p></li>
        <li><p>4</p></li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: ['show', 'leftOffset', 'showArrow']
}
</script>


<style scoped>
.dropdown-wrapper {
  position: fixed;
  top: 80px;
  right: 20px;
  width: 200px;
  height: 0px;
  background-color: #fff;
  border-radius: 5px;
}
.dropdown-wrapper p {
  color: #666;
}

.dropdown-bubble .dropdown-menu {
  display: block;
  margin-top: 1em;
}
.dropdown-menu {
  list-style-type: none;
  text-align: center;
}
.arrow {
  position: fixed;
  top: 73px;
  border: white;
  border-style: solid;  
  border-width: 10px;
  transform: rotate3d(4,4,5,70deg);
}

.dropdown-bubble .dropdown-menu:after {
  top: -.7em;
  border-bottom-color: #fff;
}
</style>



