<template>
  <div>
    <h1 class="page-title">Dashboard</h1>
    <section class="row">
      <!-- <div class="col-2"> -->
      <Card>
        <h2 slot="title">DVRs</h2>
        <a slot="add-btn" class="action" href="./#/add-dvr">+ Add New DVR</a>
        <base-table :title="table1.title" :sub-title="table1.subTitle" :data="table1.data"
                    :columns="table1.columns" :status-data="table1.statusData" :style="table1.style">
        </base-table>
      </Card>
    </section>
      <!-- </div> -->
      <!-- <div class="col-2">  -->
    <section class="row">
      <Card>
        <h2 slot="title">IP Cameras</h2>
        <a slot="add-btn" class="action" href="./#/add-cam">+ Add New IP Camera</a>
        <base-table :title="table2.title" :sub-title="table2.subTitle" :data="table2.data"
                    :columns="table2.columns" :status-data="table2.statusData" :style="table2.style">
        </base-table>
      </Card>
      <!-- </div> -->
    </section>
    <section class="row">
      <Card>
        <h2 slot="title" style="padding-bottom: 10px;">Performance</h2>
        <line-chart class="line-chart"
                    chart-id="big-line-chart"
                    :chart-data="bigLineChart.chartData"
                    :gradient-colors="bigLineChart.gradientColors"
                    :gradient-stops="bigLineChart.gradientStops"
                    :extra-options="bigLineChart.extraOptions">
        </line-chart>
      </Card>
    </section>
  </div>
</template>

<script>
import Card from './Card'
import LineChart from './Charts/LineChart'
import * as chartConfigs from './Charts/config'
import config from '../config'
import BaseTable from './BaseTable'
import { tableData, tableColumns } from '../assets/tableData'

export default {
  components: { Card, LineChart, BaseTable },
  data () {
    return {
      bigLineChart: {
        activeIndex: 0,
        chartData: {
          datasets: [{
            fill: true,
            borderColor: config.colors.primary,
            borderWidth: 2,
            borderDash: [],
            borderDashOffset: 0.0,
            pointBackgroundColor: config.colors.primary,
            pointBorderColor: 'rgba(255,255,255,0)',
            pointHoverBackgroundColor: config.colors.primary,
            pointBorderWidth: 20,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 15,
            pointRadius: 4,
            // data: this.bigLineChart.allData[index]
            data: [100, 70, 90, 70, 85, 60, 75, 60, 90, 80, 110, 100]
          }],
          labels: ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'],
        },
        extraOptions: chartConfigs.purpleChartOptions,
        gradientColors: config.colors.primaryGradient,
        gradientStops: [1, 0.4, 0],
        categories: []
      },
      // table1: {
      //   columns: [...tableColumns],
      //   data: [...tableData],
      //   style: {
      //     "width": "100%"
      //   }
      // },
      table1: {
        statusData: new Array(3).fill('offline'),
        columns: ['name', 'company', 'instance', 'status', 'settings'],
        data: [
          {
            name: 'Home',
            company: 'HikVision',
            instance: 28
          },
          {
            name: 'Workshop',
            company: 'HikVision',
            instance: 429
          },
          {
            name: 'Office',
            company: 'CP Plus',
            instance: 341
          }
        ]
      },
      table2: {
        statusData: new Array(2).fill('offline'),
        columns: ['name', 'company', 'instance', 'status', 'settings'],
        data: [
          {
            name: 'House 1',
            company: 'Mi',
            instance: 4993
          },
          {
            name: 'House 2',
            company: 'CP Plus',
            instance: 341
          }
        ]
      }
    }
  }
}
</script>

<style scoped>
  .page-title {
    font-size: 40px;
    width: 100%;
    background-color: #1E1E2F;
    position: sticky;
    top: 75px;
    padding-bottom: 10px;
  }
</style>



