<template>
  <div class="wrapper">
    <h1>Add New IP Camera</h1>
    <a class="action" href="./#/dashboard">Go Back</a>
    <Card>
      <BaseFormBig :form-data="formData">
      </BaseFormBig>
      <a class="action submit" href="./#/dashboard">+ Add IP Camera</a>      
    </Card>
  </div>
</template>

<script>
  import Card from './Card'
  import BaseFormBig from './BaseFormBig'

  export default {
    components: { Card, BaseFormBig },
    data () {
      return {
        formData: [
          {
            inputType: 'text',
            label: 'Camera Name:',
            placeholder: 'Enter Camera Name'
          },
          {
            inputType: 'text',
            label: 'Camera Company:',
            placeholder: 'Enter Camera Company'
          },
          {
            inputType: 'checkbox',
            class: 'no-username',
            label: '',
          },
          {
            inputType: 'hidden'
          },
          {
            name: 'username',
            inputType: 'text',
            label: 'Username:',
            placeholder: 'Enter Username'
          },
          {
            name: 'password',
            inputType: 'password',
            label: 'Password:',
            placeholder: 'Enter Password'
          },
          {
            inputType: 'text',
            label: 'I.P. Address:',
            placeholder: 'Enter I.P. Address'
          },
          {
            inputType: 'text',
            label: 'Number of Channels:',
            placeholder: 'Enter Number of Channels'
          }
        ]
      }
    }
  }
</script>

<style scoped>
.card {
  margin: 20px 0;
}
.textinput {
  height: 45px;
  width: 100%;
  padding: 10px;
}
a.submit {
  margin-top: 20px;
}
</style>



