<template>
  <table>
    <thead>
    <tr>
      <slot name="columns">
        <th v-for="column in columns" :key="column" :style="getStyle(column)">{{column}}</th>
      </slot>
    </tr>
    </thead>
    <tbody>
    <tr v-for="(item, row_index) in data" :key="row_index">
      <!-- <slot :row="item"> -->
        <td v-for="(column, col_index) in columns"
            :key="col_index" :style="getStyle(column)">
            <span v-if="column.toLowerCase()=='settings'">
              <a href="#/"><img class="settings" src="../assets/settings.svg" alt="s"></a>
            </span>
            <span v-else-if="column.toLowerCase()=='status'">
              <button v-if="newStatusData[row_index] !== undefined" 
                      class="status-btn" 
                      :style="statusColor(newStatusData[row_index].toLowerCase())"
                      @click="toggleStatus(row_index)">
                      {{ newStatusData[row_index] }}
              </button>
              <p v-else>error</p>
            </span>
            <span v-else>
              {{ itemValue(item, column) }}
            </span>
        </td>
      <!-- </slot> -->
    </tr>
    </tbody>
  </table>
</template>
<script>
  export default {
    name: 'base-table',
    data () {
      return {
        newStatusData: [...this.statusData]
      }
    },
    props: {
      statusData: {
        type: Array,
        default: () => [],
        description: "Status of each row of DVR/IPCam, can be online, waiting or offline."
      },
      columns: {
        type: Array,
        default: () => [],
        description: "Table columns"
      },
      data: {
        type: Array,
        default: () => [],
        description: "Table data"
      },
      type: {
        type: String, // striped | hover
        default: "",
        description: "Whether table is striped or hover type"
      },
      theadClasses: {
        type: String,
        default: '',
        description: "<thead> css classes"
      },
      tbodyClasses: {
        type: String,
        default: '',
        description: "<tbody> css classes"
      }
    },
    methods: {
      itemValue(item, column) {
        return item[column.toLowerCase()];
      },
      getStyle (column) {
        if (column.toLowerCase() === 'settings') {
          return {width: '30px', textAlign: 'center'}
        }
      },
      statusColor (status) {
        if (status === 'online') {
          return {
            color: '#42b883',
            borderColor: '#42b883'
          }
        }
        else if (status === 'wait...') {
          return {
            color: '#f7d850',
            borderColor: '#f7d850'
          }
        } else {
          return {
            color: '#e54d4d',
            borderColor: '#e54d4d'
          }
        }
      },
      toggleStatus (index) {
        if (this.newStatusData[index].toLowerCase() === 'offline')
          this.newStatusData[index] = 'online'
        else this.newStatusData[index] = 'offline'
        this.$forceUpdate()
      }
    }
  };
</script>
<style scoped>
  table {
    width: 100%;
  }
  th, td {
    padding: 10px;
    font-size: 16px;
    color: #bfbfc5;
  }
  th {
    text-align: left;
    text-transform: uppercase;
  }
  td {
    border-bottom: 1px solid #3d3f51;
  }
  .settings {
    cursor: pointer;
    height: 25px;
  }
  .status-btn {
    cursor: pointer;
    border: 2px solid;
    width: 55px;
    padding: 0 5px;
    border-radius: 1em;
    background-color: transparent;
    outline: none;
  }
</style>



