<template>
  <div id="app">
    <Navbar></Navbar>
    <div class="topbar-container">
      <div class="topbar"></div>
    </div>
    <main>
      <div class="sidebar-container">
        <Sidebar></Sidebar>
      </div>
      <div class="content">
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>

<script>
import Navbar from './components/Navbar'
import Sidebar from './components/Sidebar'

export default {
  name: 'app',
  components: { Navbar, Sidebar }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  color: white;
  cursor: default;
}

body {
  display: table;
  width: 100%;
  height: 100%;
  background-color: #1E1E2F;
}

main {
  display: flex;
  position: relative;
  top: 80px;
  min-width: 580px;
}

h1 {
  font-weight: 400;
}
h2 {
  font-weight: 400;
}
h3 {
  font-weight: 400;
}


.card-column {
  float: left;
  width: 31.33%;
  margin: 1%;
}

.row {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
}

.col-2 {
  width: 48.5%;
}

.topbar-container {
  top: 75px;
  width: 100%;
  height: 0px;
  padding: 5px 20px;
}
.topbar {
  display: none;
  position: fixed;
  width: 10%;
  min-width: 540px;
  height: 60px;
  background-color: #42b883;
  border-radius: 5px;
}

.sidebar-container {
  width: 72px;
}
.content {
  padding: 0 20px;
  width: calc(100% - 72px);
}

@media screen and (max-width: 750px) {
  .sidebar-container {
    width: 0;
  }
  .topbar-container {
    display: block;
  }
  .content {
    width: 100%;
  }
}

.line-chart {
  height: 30vw;
  max-height: 50vh;
}
@media screen and (max-width: 580px) {
  .line-chart {
    height: 174px;
  }
}

a.action {
  color: #42b883;
  font-size: 18px;
  text-shadow: 0px 0px 4px #6be2ad;
  outline: none;
  cursor: pointer;
  text-decoration: none;
}

input {
  height: 30px;
  padding: 10px;
  background-color: transparent;
  border: 1px solid #1E1E2F;
  border-radius: 5px;
}
input[type=checkbox] {
  height: 20px;
  width: 20px;
  vertical-align: middle;
}
input[type=checkbox].no-username::after {
  line-height: 20px;
  position: relative;
  left: 25px;
  content: 'No Username/Password';
  white-space: nowrap;
}

/* footer {
  border-top: 1px solid #ccc;
  position: absolute;
  width: 100%;
  bottom: 0;
  padding: 50px;
  background-color: #f00; 
} */
</style>


