<template>
  <div class="form">
    <div class="input-field" v-for="(label, index) in labels" :key="label">
      <span class="input-label">
        {{ label }}
      </span>
      <span class="input-box">
        <input :type="inputTypes[index].toLowerCase()"
              :class="classes[index]"
              :placeholder="placeholders[index]"
              :style="getStyle(inputTypes[index].toLowerCase())">
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['formData'],
  methods: {
    getStyle(inputType) {
      if (inputType === 'text' || inputType === 'password') {
        return { width: '100%' }
      }
    }
  },
  computed: {
    labels () {
      let l = []
      for (let data of this.formData) {
        if (data.label) {
          l.push(data.label)
        } else {
          l.push('');
        }
      }
      return l
    },
    placeholders () {
      let l = []
      for (let data of this.formData) {
        if (data.placeholder) {
          l.push(data.placeholder)
        } else {
          l.push('');
        }
      }
      return l
    },
    inputTypes () {
      let l = []
      for (let data of this.formData) {
        if (data.inputType) {
          l.push(data.inputType)
        } else {
          l.push('');
        }
      }
      return l
    },
    classes () {
      let l = []
      for (let data of this.formData) {
        if (data.class) {
          l.push(data.class)
        } else {
          l.push('');
        }
      }
      return l
    }
  }
}
</script>

<style scoped>
  .form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .input-field {
    width: 100%;
    padding: 5px 5px 5px 5px;
  }
  .input-label {
    display: inline-block;
    width: 40%;
    text-align: right;
    padding-right: 10px;
  }
  .input-box {
    display: inline-block;
    width: 59%;
  }
</style>



