<template>
  <div class="card">
    <div style="display: flex; justify-content: space-between; padding: 0 10px;">
      <slot name="title"><wbr></slot>
      <slot name="add-btn"><wbr></slot>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'card'
}
</script>


<style scoped>
.card {
  width: 100%;
  height: 100%;
  padding: 20px;
  background-color: #27293D;
  /* border: 1px solid #ccc; */
  border-radius: 5px;
}
.card-heading {
  font-size: 32px;
  text-align: center;
}
</style>


