<template>
  <nav>
    <!-- <div class="navbar-left">
      <ul>
        <li><p>Help</p></li>
        <li><p>Community</p></li>
        <li><p>Status</p></li>
      </ul>
    </div> -->
    <div class="navbar-logo">
      <ul>
        <li><h2>Codenscious.AI</h2></li>
      </ul>
    </div>
    <div class="navbar-right">
      <ul>
        <li>
          <img @click="toggleDropdown('apps', $event)" class="navbar-item" 
               src="../assets/apps.svg" alt="apps">
          <Dropdown :show="dropdownStyle('apps')" 
                    :left-offset="getOffset('apps')"
                    :show-arrow="showArrow('apps')"></Dropdown>
        </li>
        <li>
          <img @click="toggleDropdown('settings', $event)"
               class="navbar-item"
               src="../assets/settings.svg" alt="settings">
          <Dropdown :show="dropdownStyle('settings')" 
                    :left-offset="getOffset('settings')"
                    :show-arrow="showArrow('settings')"></Dropdown>
        </li>
        <li>
          <img @click="toggleDropdown('profile', $event)" class="profile" src="../assets/default-avatar.png" alt="avatar">
          <Dropdown :show="dropdownStyle('profile')" 
                    :left-offset="getOffset('profile')"
                    :show-arrow="showArrow('profile')"></Dropdown>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import Dropdown from './Dropdown'

export default {
  components: { Dropdown },
  data () {
    return {
      dropdowns: {
        settings: false,
        apps: false,
        profile: false
      },
      offsets: {
        settings: 0,
        apps: 0,
        profile: 0
      }
    }
  },
  methods: {
    toggleDropdown (name, event) {
      for (let key of Object.keys(this.dropdowns)) {
        if (key === name) this.dropdowns[name] = !this.dropdowns[name]
        else this.dropdowns[key] = false
      }
      this.offsets[name] = window.innerWidth - event.target.x - event.target.width/2 - 27
    },
    dropdownStyle (name) {
      if (this.dropdowns[name]) {
        return {
          display: 'block',
          height: 'unset'
        }
      } else {
        return {
          display: 'none',
          height: '0px'
        }
      }
    },
    getOffset (name) {
      if (this.dropdowns[name]) {
        return {
          display: 'block',
          right: `${this.offsets[name]}px`
        } 
      } else {
        return {
          display: 'none'
        }
      } 
    },
    showArrow (name) {
      if (this.dropdowns[name]) {
        return {
          display: 'block',
        }
      } else {
        return {
          display: 'none',
        }
      }
    }
  }
}
</script>


<style scoped>
nav {
  position: fixed;
  width: 100%;
  height: 75px;
  padding: 0 20px;
  z-index: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
  background-color: #1E1E2F;
}
nav ul {
  height: 75px;
  list-style-type: none;
}
nav li {
  border: 8px solid transparent;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
  height: 75px;
  /* background-color: #f00; */
}
nav p {
  /* background-color: #0f0; */
  vertical-align: middle;
  font-size: 16px;
  color: white;
}
nav img {
  height: 50%;
}
nav .profile {
  height: 70%;
  border-radius: 50%;
  cursor: pointer;
}
.navbar-item:hover {
  filter: drop-shadow(0px 0px 2px #fff);
}
/* @media screen and (max-width: 600px) {
  .navbar-right {
    display: none;
  }
} */
</style>



